import axios from 'axios'
import {API_V1_URL} from '@/config'

export default {
  create(name) {
    return axios.post(API_V1_URL + '/cgroups', {name})
      .then(res => res.data)
  },
  update({id, name}) {
    return axios.patch(API_V1_URL + '/cgroups/' + id, {name})
  },
  delete(groupId) {
    return axios.delete(API_V1_URL + '/cgroups/' + groupId)
  },
  getList() {
    return axios.get(API_V1_URL + '/cgroups')
      .then(res => res.data)
  },
  getGroupClients(groupId) {
    return axios.get(API_V1_URL + '/cgroups/' + groupId)
  },
  addClientToGroup(groupId, clientId) {
    // TODO
    return false
  },
  removeClientFromGroup(groupId, clientId) {
    // TODO
    return false
  },
}



