import axios from 'axios'
import userService from '@/services/userService'
import {apiToUser} from '@/services/converters'
import {API_V1_URL} from '@/config'

function createUser(user) {
  return axios.post(API_V1_URL + '/users', user)
    .then(res => apiToUser(res.data))
}

function getUsersByRole(role) {
  return axios.get(API_V1_URL + '/users?role=' + role)
    .then(res => res.data.map(apiToUser))
}

export default {
  ...userService,
  getUser(id) {
    return axios.get(API_V1_URL + '/user/' + id)
      .then(res => apiToUser(res.data))
  },
  createUser(user) {
    user.role = 'admin'
    return createUser(user)
  },
  createClient(client) {
    client.role = 'client'
    return createUser(client)
  },
  getClients() {
    return getUsersByRole('client')
  },
  getUsers() {
    return getUsersByRole('admin')
  },
  // AUTH
  sendMailForNewPass(email) {
    // It will trigger the server to send an email with a link for password reset
    return axios.post(API_V1_URL + '/credentials/lost', {email})
  },
}



