<template>
  <table class="table table-bordered">
    <tbody>

      <tr>
        <th scope="row">Name:</th>
        <td>{{ client.name }}</td>
      </tr>

      <tr>
        <th scope="row">Client ID:</th>
        <td>{{ client.clientId }}</td>
      </tr>

      <tr>
        <th scope="row">Email:</th>
        <td>{{ client.email }}</td>
      </tr>

      <tr>
        <th scope="row">Phone:</th>
        <td>{{ client.phone }}</td>
      </tr>

      <tr>
        <th scope="row">Contract Id:</th>
        <td>{{ client.contractId }}</td>
      </tr>

      <tr>
        <th scope="row">Portfolio Id:</th>
        <td>{{ client.portfolios[0] }}</td>
      </tr>

      <tr>
        <th scope="row">Groups:</th>
        <td>
          <ul>
            <li v-for="group in groups">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" v-model="group.name" :id="group.name" @change="toggleGroup($event, group)">
                <label class="form-check-label" :for="group.name">{{ group.name }}</label>
              </div>
            </li>
          </ul>
        </td>
      </tr>

    </tbody>
  </table>
</template>


<script>
export default {
  props: ['client', 'groups'],
  methods: {
    toggleGroup(event, group) {
      if (event.target.checked) {
        // Add the client to the group
        // TODO
      } else {
        // Remove the client from the group
        // TODO
      }
    },
  },
}
</script>



